* {
  box-sizing: border-box;
  font-family: Inter, sans-serif;
}

body, p, b {
  margin: 0;
}

body {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.text-gray {
  color: #9f9f9f;
}

.body-container {
  width: 90%;
  max-width: 1120px;
}

.header-container {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-top: 36px;
  display: flex;
}

.header-item {
  align-items: center;
  gap: 24px;
  display: flex;
}

.header-item > p {
  cursor: pointer;
  color: #1a1a1a;
  font-size: 12.8px;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
}

.title-container {
  color: #000;
  width: 100%;
  margin: 91px 0 64px;
  font-family: Poppins;
  font-size: 48.83px;
  font-style: normal;
  font-weight: 600;
  line-height: 130%;
}

.title-container > p {
  max-width: 559px;
}

.submit-form-container {
  justify-content: space-between;
  display: flex;
}

.submit-form-left {
  flex-direction: column;
  gap: 48px;
  max-width: 640px;
  display: flex;
}

.submit-form-right {
  text-align: right;
  font-family: Poppins;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
}

.submit-form-right > p {
  white-space: nowrap;
}

.interest-form {
  width: 100%;
}

.interest-form > input {
  color: #9f9f9f;
  border: none;
  outline: none;
  width: 100%;
  font-family: Poppins;
  font-size: 25px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
}

.interest-form > p {
  margin-top: 16px;
}

.interest-form > p > a {
  color: #fefefe;
  background: var(--black-000000, #000);
  cursor: pointer;
  border: none;
  border-radius: 103px;
  outline: none;
  padding: 10px 16px;
  font-family: Poppins;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  text-decoration: none;
}

.interest-form > p > a:last-child {
  color: var(--black-000000, #000);
  border: 1px solid var(--black-000000, #000);
  background: var(--white-ffffff, #fff);
}

.name-container {
  color: var(--medium-grey-9-f-9-f-9-f, #9f9f9f);
  border: none;
  border-bottom: 1px solid #9f9f9f;
  outline: none;
  width: 100%;
  height: 86px;
  font-family: Poppins;
  font-size: 25px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
}

.submit-form-left > textarea {
  color: var(--medium-grey-9-f-9-f-9-f, #9f9f9f);
  border: none;
  border-bottom: 1px solid #9f9f9f;
  outline: none;
  height: 174px;
  font-family: Poppins;
  font-size: 25px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
}

.submit-form-left > button {
  color: var(--light-grey-fefefe, #fefefe);
  cursor: pointer;
  background: var(--black-000000, #000);
  border: none;
  border-radius: 8px;
  outline: none;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: -moz-fit-content;
  width: fit-content;
  padding: 16px 24px;
  font-family: Poppins;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  display: flex;
}

.mobile-bar-ico {
  display: none;
}

.footer-container {
  border-top: 1px solid #0000004d;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-top: 160px;
  padding: 48px 0 96px;
  display: flex;
}

.footer-menu {
  align-items: center;
  gap: 48px;
  margin-top: 64px;
  display: flex;
}

.footer-menu > p {
  color: #9f9f9f;
  text-align: center;
  cursor: pointer;
  font-family: Inter;
  font-size: 12.8px;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
}

.footer-social {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.footer-social > p {
  align-items: center;
  gap: 32px;
  display: flex;
}

.footer-social > p > img {
  cursor: pointer;
}

.mobile-side-bar {
  z-index: 10;
  background: #2c3e50;
  flex-direction: column;
  align-items: flex-end;
  padding: 20px;
  display: none;
  position: fixed;
  inset: 0;
}

.mobile-side-bar > p {
  color: #fff;
  cursor: pointer;
  font-size: 25px;
}

.mobile-side-bar-wrapper {
  color: #fff;
  text-align: right;
  flex-direction: column;
  gap: 20px;
  margin-top: 20px;
  font-size: 23px;
  display: flex;
}

.mobile-side-bar-wrapper > p {
  cursor: pointer;
  transition: all .3s ease-in-out;
}

.mobile-side-bar-wrapper > p:hover {
  color: #f57520;
}

@media screen and (width <= 800px) {
  .title-container {
    text-align: center;
    justify-content: center;
    margin-top: 50px;
    font-size: 35px;
    display: flex;
  }

  .title-container > p {
    max-width: 500px;
  }

  .header-item {
    display: none;
  }

  .mobile-bar-ico {
    cursor: pointer;
    width: 30px;
    height: 30px;
    display: flex;
  }

  .submit-form-left {
    gap: 25px;
    max-width: 100%;
    margin-right: 20px;
  }

  .interest-form > input {
    font-size: 18px;
  }

  .name-container {
    height: 50px;
    font-size: 18px;
  }

  .submit-form-left > textarea {
    height: 120px;
    font-size: 18px;
  }

  .interest-form > p {
    margin-top: 7px;
  }

  .interest-form > p > a, .submit-form-left > button {
    font-size: 12px;
  }

  .footer-container {
    margin-top: 70px;
  }
}

@media screen and (width <= 700px) {
  .submit-form-container {
    flex-direction: column;
  }

  .submit-form-right {
    text-align: left;
    margin-top: 30px;
  }
}

@media screen and (width <= 480px) {
  .footer-menu {
    flex-direction: column;
    gap: 20px;
    width: 100%;
    margin-top: 40px;
  }

  .footer-menu > p {
    text-align: left;
    width: 100%;
  }

  .title-container {
    font-size: 25px;
  }

  .footer-social > p {
    gap: 25px;
  }

  .footer-social > p > img {
    width: 18px;
    height: 18px;
  }

  .interest-form > input {
    font-size: 15px;
  }

  .name-container {
    height: 40px;
    font-size: 15px;
  }

  .submit-form-left > textarea {
    font-size: 15px;
  }

  .interest-form > p {
    justify-content: center;
    gap: 10px;
    width: 100%;
    display: flex;
  }
}

@media screen and (width <= 400px) {
  .interest-form > p {
    flex-direction: column;
  }

  .submit-form-right > p {
    white-space: normal;
  }

  .interest-form > p > a {
    text-align: center;
  }
}

